<template>
  <div class="cms-form-layout">
    <cms-prompt collapse='true'>
      <li> 设置微信开放平台应用，设置成功之后即可使用授权方式接入微信公众号</li>
      <li> 选项不可填写的为系统与微信开放平台对接的固定信息，不能修改，可将自动生成好的信息直接填入微信开放平台应用中</li>
    </cms-prompt>
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" v-loading='loading'>
        <h3 class="cms-segmentation">
        应用基本信息
      </h3>
       <el-form-item label="名称" prop="name" :error='errorMessage.name'>
        <el-input v-model="formData.name" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="AppId" prop="appId" :error='errorMessage.appId'>
        <el-input v-model="formData.appId" maxlength="50"></el-input>
      </el-form-item>
      <el-form-item label="AppSecret" prop="appSecret" :error='errorMessage.appSecret'>
        <el-input v-model="formData.appSecret" maxlength="100"></el-input>
      </el-form-item>
      <h3 class="cms-segmentation">
        授权流程相关
      </h3>
      <el-form-item label="授权发起页域名" prop="smtpService" :error='errorMessage.smtpService'>
        <span class="form-text"> {{baseUrl}}</span>
        <div class="gray">{domain}为程序部署域名，在创建微信开放平台应用时，请将该地址中{domain}更改成实际访问域名然后再填入对应的选项中</div>
      </el-form-item>
      <el-form-item label="授权事件接收URL" prop="smtpPort" :error='errorMessage.smtpPort'>
        <span class="form-text">{{baseUrl}}/weChat/authCallBack</span>
        <div class="gray">{domain}为程序部署域名，在创建微信开放平台应用时，请将该地址中{domain}更改成实际访问域名然后再填入对应的选项中</div>
      </el-form-item>
      <h3 class="cms-segmentation">
        授权后实现业务
      </h3>
      <el-form-item label="消息校验Token" prop="messageValidateToken" :error='errorMessage.messageValidateToken'>
        <el-input v-model="formData.messageValidateToken" maxlength="50">
          <el-button slot="append" @click="createMessageValidateToken">自动生成</el-button>
        </el-input>
        <span class="gray">只能是字母和数字，如不清楚如何获取规定字符串，请点击自动生成，再将生成字符串填入微信开放平台</span>
      </el-form-item>
      <el-form-item label="消息加解密Key" prop="messageDecryptKey" :error='errorMessage.messageDecryptKey'>
        <el-input v-model="formData.messageDecryptKey" maxlength="50">
          <el-button slot="append" @click="createMessageDecryptKey">自动生成</el-button>
        </el-input>
        <span class="gray">必须是长度为43位的字符串，只能是字母和数字，如不清楚如何获取规定字符串，请点击自动生成，再将生成字符串填入微信开放平台</span>
      </el-form-item>
      <el-form-item label="消息与事件接收URL" prop="testemail" :error="errorMessage.testemail">
        <span class="form-text"> {{baseUrl}}/weChat/keyword/reply/$APPID$</span>
        <div class="gray">{domain}为程序部署域名，在创建微信开放平台应用时，请将该地址中{domain}更改成实际访问域名然后再填入对应的选项中（注：$APPID$不需要替换）</div>
      </el-form-item>
      <el-form-item label="公众号开发域名" prop="testemail" :error="errorMessage.testemail">
        <span class="form-text"> {{baseUrl}}</span>
        <div class="gray">domain}为程序部署域名，在创建微信开放平台应用时，请将该地址中{domain}更改成实际访问域名然后再填入对应的选项中</div>
      </el-form-item>

    </el-form>
    <div class="cms-form-footer">
      <cms-button @click="saveData()" v-color>保存</cms-button>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { random } from './random.js'
import {
  weChatSetGet,
  weChatSetUpdate,
  weChatSetSave
} from '@/api/wechat/index.js'
var defaultData = {
  name: '',
  logo_id: '',
  appId: '',
  appSecret: '',
  messageDecryptKey: '',
  messageValidateToken: ''
}
export default {
  name: 'wxConfig',
  mixins: [form],
  data() {
    return {
      baseUrl: '{domain}',
      formData: Object.assign({}, defaultData),
      rules: {
        name: [rules.required()],
        appId: [rules.required()],
        appSecret: [rules.required()],
        messageValidateToken: [
          rules.required(),
          rules.english(),
          rules.$length(16)
        ],
        messageDecryptKey: [
          rules.required(),
          rules.english(),
          rules.$length(43)
        ]
      }
    }
  },
  methods: {
    createMessageValidateToken() {
      this.formData.messageValidateToken = random(16)
    },
    createMessageDecryptKey() {
      this.formData.messageDecryptKey = random(43)
    },
    getFormData() {
      weChatSetGet().then(res => {
        if (res.code === 200 && res.data !== null) {
          this.formData = res.data
        }
      })
    },
    saveData() {
      if (this.formData.id && this.formData.id !== '') {
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.loading = true
            weChatSetUpdate(this.formData).then(res => {
              this.loading = false
              if (res.code === 200) {
                this.$alert('配置参数更新成功，请等待10分钟左右微信服务器成功推送component_verify_ticket后才能正常使用系统内微信菜单相关功能！', '系统提示', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                })
              } else {
                this.loading = false
                this.$errorMessage(res.code + ':' + res.message)
              }
            })
          }
        })
      } else {
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.loading = true
            weChatSetSave(this.formData).then(res => {
              this.loading = false
              if (res.code === 200) {
                this.$alert('配置参数更新成功，请等待10分钟左右微信服务器成功推送component_verify_ticket后才能正常使用系统内微信菜单相关功能！', '系统提示', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                })
              } else {
                this.loading = false
                this.$errorMessage(res.code + ':' + res.message)
              }
            })
          }
        })
      }
    }
  },
  created() {
    this.getFormData()
  }
}
</script>
<style lang="scss" >
</style>




